// src/components/admin/trash/TrashFilters.jsx
import React from "react";
import PropTypes from "prop-types";
import Flatpickr from "react-flatpickr";
import { Russian } from "flatpickr/dist/l10n/ru.js";
import "flatpickr/dist/flatpickr.min.css";

const TrashFilters = ({
  searchFilter,
  dateRange,
  onSearchChange,
  onDateChange,
  onClear,
  onPurgeAll,
  purgeDisabled,
}) => {
  const datePickerConfig = {
    mode: "range",
    altInput: true,
    altFormat: "F j, Y",
    dateFormat: "Y-m-d",
    locale: Russian,
    onChange: (dates) => onDateChange(dates),
  };

  return (
    <div className="filters-container">
      {/*   / */}
      <div className="filter-group">
        <label htmlFor="search">  :</label>
        <input
          id="search"
          type="text"
          placeholder="    "
          value={searchFilter}
          onChange={(e) => onSearchChange(e.target.value)}
        />
      </div>

      {/*    */}
      <div className="filter-group">
        <label htmlFor="date-filter">  :</label>
        <Flatpickr
          id="date-filter"
          options={datePickerConfig}
          value={dateRange}
          placeholder=" "
          className="date-input"
        />
      </div>

      {/*   */}    
      <button
        onClick={onClear}
        className="custom_button_long"
        disabled={!searchFilter && !dateRange[0]}
      >
         
      </button>

      {/*    */}
      {!purgeDisabled && (
        <button
          onClick={onPurgeAll}
          className="custom_button_long action-remove"
        >
           
        </button>
      )}
    </div>
  );
};

TrashFilters.propTypes = {
  searchFilter: PropTypes.string.isRequired,
  dateRange: PropTypes.arrayOf(PropTypes.instanceOf(Date)).isRequired,
  onSearchChange: PropTypes.func.isRequired,
  onDateChange: PropTypes.func.isRequired,
  onClear: PropTypes.func.isRequired,
  onPurgeAll: PropTypes.func.isRequired,
  purgeDisabled: PropTypes.bool.isRequired,
};

export default React.memo(TrashFilters);
