import { toast } from "react-toastify";

// const API_URL = "http://127.0. 0.1:5000/api";

const API_URL = "https://webnews-1fwz.onrender.com/api";


let authToken = null;
let lastErrorMessage = ""; //     

export const initAuthToken = () => {
  try {
    const userData = localStorage.getItem("user") || sessionStorage.getItem("user");
    if (userData) {
      const user = JSON.parse(userData);
      if (user?.token) {
        authToken = user.token;
        api.setAuthToken(authToken);
      }
    }
  } catch (error) {
    console.error("Failed to initialize auth token:", error);
    clearAuthData();
  }
};


export const clearAuthData = () => {
  authToken = null;
  localStorage.removeItem("user");
  sessionStorage.removeItem("user");
};

//     ,   (,  )
export const resetErrorFlag = () => {
  lastErrorMessage = "";
};

const handleResponse = async (response) => {
  if (!response.ok) {
    //   JSON- (  )
    let errorData = {};
    try {
      errorData = await response.json();
    } catch (e) {
      //    JSON   
    }

    //        
    const errorMessage =
      errorData.error || "    ";

    //            
    if (errorMessage !== lastErrorMessage) {
      toast.error(errorMessage);
      lastErrorMessage = errorMessage;
    }

    //  ,      
    switch (response.status) {
      case 401:
        throw new Error(errorMessage || "   ");
      case 403:
        throw new Error(errorMessage || " ");
      case 404:
        throw new Error(errorMessage || "  ");
      case 409:
        throw new Error(errorMessage || " ");
      case 500:
        throw new Error(errorMessage || "  ");
      default:
        throw new Error(errorMessage);
    }
  }

  //    OK,    JSON (  )   
  try {
    return await response.json();
  } catch {
    throw new Error("    ");
  }
};

export const api = {
  setAuthToken: (token) => {
    authToken = token;
  },

  getAuthToken: () => authToken,

  ping: async () => {
    try {
      const response = await fetch(`${API_URL}/ping`, {
        method: "GET",
        credentials: "include",
      });
      return response.ok;
    } catch (error) {
      console.error(" :", error);
      return false;
    }
  },

  get: async (path) => {
    //      lastErrorMessage,
    //       ,   .
    lastErrorMessage = "";

    const headers = {};
    if (authToken) {
      headers["Authorization"] = `Bearer ${authToken}`;
    }

    const response = await fetch(`${API_URL}${path}`, {
      method: "GET",
      headers,
      credentials: "include",
    });

    return await handleResponse(response);
  },

  post: async (path, data, isFormData = false) => {
    lastErrorMessage = "";

    const headers = {};
    if (authToken) {
      headers["Authorization"] = `Bearer ${authToken}`;
    }
    //   FormData,  Content-Type
    if (!isFormData) {
      headers["Content-Type"] = "application/json";
    }

    const options = {
      method: "POST",
      headers,
      credentials: "include",
      body: isFormData ? data : JSON.stringify(data),
    };

    const response = await fetch(`${API_URL}${path}`, options);
    return await handleResponse(response);
  },

  put: async (path, data, isFormData = false) => {
    lastErrorMessage = "";

    const headers = {};
    if (authToken) {
      headers["Authorization"] = `Bearer ${authToken}`;
    }
    if (!isFormData) {
      headers["Content-Type"] = "application/json";
    }

    const options = {
      method: "PUT",
      headers,
      credentials: "include",
      body: isFormData ? data : JSON.stringify(data),
    };

    const response = await fetch(`${API_URL}${path}`, options);
    return await handleResponse(response);
  },

  delete: async (path) => {
    lastErrorMessage = "";

    const headers = {};
    if (authToken) {
      headers["Authorization"] = `Bearer ${authToken}`;
    }

    const response = await fetch(`${API_URL}${path}`, {
      method: "DELETE",
      headers,
      credentials: "include",
    });

    return await handleResponse(response);
  },
};
